@import "../../common/_variable";
@import "../../common/_mixin";
@import "../../common/_common";

/* NHN WebStandard Team (http://html.nhndesign.com/) author : MJ,Yoon */

* {margin:0; padding:0; color:#444444; font-size:12px; font-family:"나눔고딕",NanumGothic, "돋움", dotum;}
img, fieldset, button {border:0;}
hr, button img {display:none;}
li {list-style:none;}
img{vertical-align:top;}
a {text-decoration:none;}
a:hover, a:active, a:focus {text-decoration:underline;}

body {text-align:center; font-family:dotum, sans-serif;}
#pop_wrap { position:relative; background:#fff; text-align:left; font-size:12px;}

#pop_wrap .header{border-bottom:1px solid #eeeeec; background:none; background-color:#f4f4f3;}
#pop_wrap .header h1 {padding:11px 0 10px 21px; font-size:14px; word-spacing:-1px;}

.notice {width:530px;}

#pop_wrap .content {overflow:hidden; overflow-y:auto; width:530px; height:345px;}
#pop_wrap .content .article {padding:16px 20px 30px 19px; color:#333; line-height:18px; word-spacing:-1px; font-family:"나눔고딕",NanumGothic, "돋움", dotum;}
#pop_wrap .content .article br {letter-spacing:0;}

#pop_wrap .footer {position:relative; margin:0 20px; padding-top:10px; border-top:1px solid #eeeeec; text-align:center; }
#pop_wrap .footer .close {text-align:right;}
#pop_wrap .footer .close label {font-family:"돋움", dotum; font-size:11px; letter-spacing:-1px; color:#666;}
#pop_wrap .footer .close input {width:13px; height:13px; margin:-2px 0 2px 3px;}